import React from 'react';
import {Link} from 'react-router-dom';

class Home extends React.Component {
	componentWillMount() {
		this.props.exitGame();
	}

	render() {
		return (
			<div id="container" className="text-center">
				<div className="row">
					<div className="col-12 mt-5">
						<h1>Square Game</h1>
					</div>
				</div>

				<div className="row">
					<div className="col-12 mt-5">
						<Link to="/board" className="btn btn-success btn-lg" >Comenzar juego</Link>
					</div>
				</div>

				<div>{this.props.gameState}</div>
			</div>
		)
	}
}

export default Home;