// funcion que selecciona un color aleatorio
function randomColor() {
    var colors=['#A4C400', '#60A917', '#008A00', '#00ABA9', '#1BA1E2', '#0050EF', '#6A00FF', '#AA00FF',
        '#F472D0', '#D80073', '#A20025', '#E51400', '#FA6800', '#F0A30A', '#E3C800', '#825A2C'];
    return colors[Math.floor(Math.random()*16)];
};
// funcion que selecciona un caracter aleatorio
function randomChar() {
    return icons[Math.floor(Math.random()*icons.length)].c;
};
// funcion que revuelve un arreglo
function shuffle(array) {
    var currentIndex=array.length, temporaryValue, randomIndex;

    while(0!==currentIndex) {
        randomIndex=Math.floor(Math.random()*currentIndex);
        currentIndex-=1;

        temporaryValue=array[currentIndex];
        array[currentIndex]=array[randomIndex];
        array[randomIndex]=temporaryValue;
    }

    return array;
}

// funcion que hace el arreglo de cuadrados
function squares(n) {
    var squareArr=[];
    for(var i=0; i<n/2; i++) {
        var square={
            backgroundColor:randomColor(),
            data:Date.now()+i,
            id:i,
            character:randomChar(),
            state:'normal'
        };
        squareArr.push(square);
        squareArr.push(Object.assign({}, square, {id:n/2+i}));
    }
    return shuffle(squareArr);
};

//// API
export function getSquares(n) {
    return new Promise(function(resolve) {
        setTimeout(function() {
            resolve(squares(n));
        }, 500);
    });
};