import React from 'react';

class Square extends React.Component {
	constructor(props) {
		super(props);
		this.onClick=this.onClick.bind(this);
	}


	//////////
	// HELPERS
	onClick() { 
		if(this.props.state==='normal')
		this.props.onClick(this.props.id);
	}
	//////////


    render() {
        const divStyle={
        	'color':'#FFFFFF',
        	'backgroundColor':this.props.state==='dead'?'#FFFFFF':this.props.backgroundColor
        }

        return (
        	<button
        		key={this.props.id}
        		id={this.props.id}
        		className="btn btn-default square"
        		style={divStyle}
        		disabled={this.props.state==='selected' || this.props.state==='dead'}
        		onClick={this.onClick}
        		>
        		<i className={'fa '+this.props.character}></i>
        	</button>
    	);
    }
}

export default Square;