import * as types from './types';

export const initialState={
	gameState:null,		// null, loading, playing, paused
	squares:[],
	score:0,
	countdown:0,
	timer:0,
	selected:null
}

export const reducer=(state=initialState, action)=> {
	//return state;

	switch(action.type) {
		case types.LOAD:
			console.log('ST::cargando');
			return {...state, gameState:'loading', countdown:3};
		case types.PLAY:
			console.log('ST::jugando');
			return {...state, gameState:'playing', timer:10};
		case types.PAUSED:
			console.log('ST::en pausa');
			return {...state, gameState:'paused'};
		case types.EXIT_GAME:
			console.log('ST::saliendo del juego');
			return {...state, gameState:null};

		case types.GET_SQUARES_SUCCESS:
			console.log('ST::cuadros listos');
			return {...state, squares:action.squares};
		case types.UPDATE_COUNTDOWN:
			return {...state, countdown:action.countdown};
		case types.UPDATE_TIMER:
			return {...state, timer:action.timer};
        case types.CLICK_ON_SQUARE:
            var nextState=cloneState(state);
            // si no se ha seleccionado ningun cuadro
            if(nextState.selected===null) {
                nextState.selected=action.id;
                nextState.squares.forEach(function(s) { if(s.id===action.id) s.state='selected'; });
            }
            // si ya se había seleccionado un cuadro
            else {
                var prevData=nextState.squares.filter(s=> s.id===nextState.selected)[0].data;
                var currData=nextState.squares.filter(s=> s.id===action.id)[0].data;
                // si no coinciden
                if(prevData!==currData) {
                    nextState.squares.forEach(function(x) { if(x.state!=='dead') x.state='normal'; });
                    nextState.selected=null;
                    nextState.score-=15;
                }
                
                // si si coinciden
                else {
                    nextState.squares.forEach(function(x) {
                        if(x.id===nextState.selected || x.id===action.id || x.state==='dead') x.state='dead';
                        else x.state='normal'
                    });
                    nextState.selected=null;
                    nextState.score+=10;
                }
            }
            return nextState;
            break;

		default:
			return state;
	}
}


// HELPERS
function cloneState(state) {
    var newState=Object.assign({}, state);
    newState.squares=state.squares.slice();
    return newState;
}