import React from 'react';

class Timer extends React.Component {
	constructor(props) {
		super(props);
		this.updateTimer=this.updateTimer.bind(this);
		this.load=this.load.bind(this);
	}

	componentDidMount() {
        this.tick=setInterval(function() {
            if(this.props.timer>0) this.updateTimer(this.props.timer-1);
            if(this.props.timer===0) this.load();
        }.bind(this), 1000);
	}

	componentWillUnmount() { clearInterval(this.tick); }

	//////////
	// HELPERS
	updateTimer(timer) { this.props.updateTimer(timer); }
	load() { this.props.load(); }
	//////////

	render() {
		return <span>{this.props.timer}</span>
	}
}

export default Timer;