import * as actions from './actionCreators';
import * as types from './types';
import * as api from '../api/api';

export const dataService=store=> next=> action=> {
    switch(action.type) {
        case types.GET_SQUARES:
        	console.log('MW::trayendo cuadros');
            api.getSquares(action.n)
            	.then(squares=> next(actions.getSquaresSuccess(squares)));
        	break;
        default:
        	next(action);
    }
};