import React from 'react';

class Countdown extends React.Component {
	constructor(props) {
		super(props);
		this.updateCountdown=this.updateCountdown.bind(this);
		this.play=this.play.bind(this);
	}

	componentDidMount() {
        this.tick=setInterval(function() {
            if(this.props.countdown>0) this.updateCountdown(this.props.countdown-1);
            if(this.props.countdown===0) this.play();
        }.bind(this), 1000);
	}

	componentWillUnmount() { clearInterval(this.tick); }

	//////////
	// HELPERS
	updateCountdown(countdown) { this.props.updateCountdown(countdown); }
	play() { this.props.play(); }
	//////////

	render() {
		return <span>{this.props.countdown}</span>
	}
}

export default Countdown;