import React from 'react';
import Countdown from './Countdown';
import Timer from './Timer';
import Square from './Square';

class Board extends React.Component {
	constructor(props) {
		super(props);
		this.updateCountdown=this.updateCountdown.bind(this);
		this.updateTimer=this.updateTimer.bind(this);
		this.play=this.play.bind(this);
		this.load=this.load.bind(this);
		this.onClick=this.onClick.bind(this);
	}

	componentWillMount() { this.load(); }

	//////////
	// HELPERS
	updateCountdown(countdown) { this.props.updateCountdown(countdown); }
	updateTimer(timer) { this.props.updateTimer(timer); }
	play() { this.props.play(); }
	load() { this.props.load(); }
	onClick(id) {this.props.onClick(id); }
	//////////

	render() {
		////////////////////////////////////////////
		// si esta cargando mostrar cuenta regresiva
		if(this.props.gameState==='loading')
			return (
				<div className="row">
					<div className="col-12 car card-default">
						<div className="card-body text-center">
							<h1 className="mt-5 mb-5">
								<Countdown
									countdown={this.props.countdown}
									updateCountdown={this.updateCountdown}
									play={this.play}
									/>
							</h1>
						</div>
					</div>
				</div>
			)

		////////////////
		// mostrar juego
		return (
			<div className="row">
				<div className="col-12 card card-default">
					<div className="card-heading">
						<div className="float-left">
							{this.props.score}
						</div>

						<div className="float-right">
							<Timer
								timer={this.props.timer}
								updateTimer={this.updateTimer}
								load={this.load}
								/>
						</div>
					</div>

					<div className="card-body">
						{this.props.squares.map(square=> <Square {...square} onClick={this.onClick} />)}
					</div>
				</div>
			</div>
		)
	}
}

export default Board;