import * as types from './types';

// navegacion y pantallas
export const load=()=> {
	return {
		type:types.LOAD
	};
};

export const play=()=> {
	return {
		type:types.PLAY
	};
};

export const paused=()=> {
	return {
		type:types.PAUSED
	};
};

export const exitGame=()=> {
	return {
		type:types.EXIT_GAME
	};
};


// api
export const getSquares=(n)=> {
	return {
		type:types.GET_SQUARES,
		n:n
	};
};
export const getSquaresSuccess=(squares)=> {
	return {
		type:types.GET_SQUARES_SUCCESS,
		squares:squares
	};
};
export const updateCountdown=(countdown)=> {
	return {
		type:types.UPDATE_COUNTDOWN,
		countdown:countdown
	};
};
export const updateTimer=(timer)=> {
	return {
		type:types.UPDATE_TIMER,
		timer:timer
	};
};
export const clickOnSquare=(id)=> {
	return {
		type:types.CLICK_ON_SQUARE,
		id:id
	};
};