import React from 'react';
import {BrowserRouter as Router, Route} from 'react-router-dom';
import {createStore, applyMiddleware} from 'redux';
import * as reducers from './redux/reducers.js';
import * as actions from './redux/actionCreators';
import {dataService} from './redux/middleware';

// componentes
import Home from './components/Home';
import Board from './components/Board';

// estilos
import 'bootstrap-css-only/css/bootstrap.min.css';
import 'font-awesome/css/font-awesome.css';
import './App.css';

// store
const store=createStore(reducers.reducer, reducers.initialState, applyMiddleware(dataService));

// componente
class App extends React.Component {
	constructor(props) {
		super(props);

		this.state=store.getState();

		store.subscribe(()=> { this.setState(store.getState()); });

		// bindings
		this.load=this.load.bind(this);
		this.play=this.play.bind(this);
		this.paused=this.paused.bind(this);
		this.exitGame=this.exitGame.bind(this);
		this.updateCountdown=this.updateCountdown.bind(this);
		this.updateTimer=this.updateTimer.bind(this);
		this.onClick=this.onClick.bind(this);
	}

	//////////
	// HELPERS
	load() {
		store.dispatch(actions.load());
		store.dispatch(actions.getSquares(40));
	}
	play() { store.dispatch(actions.play()); }
	paused() { store.dispatch(actions.paused()); }
	exitGame() { store.dispatch(actions.exitGame()); }
	updateCountdown(countdown) { store.dispatch(actions.updateCountdown(countdown)); }
	updateTimer(timer) { store.dispatch(actions.updateTimer(timer)); }
	onClick(id) { store.dispatch(actions.clickOnSquare(id)); }
	//////////

	render () {
		return (
			<div id="main-container" className="fluid-container">
				<div className="col-12 h-100 justify-content-center align-items-center">
					<Router>
						<Route exact path="/" render={()=> <Home
							{...this.state}
							exitGame={this.exitGame} 
							/>} />
						<Route path="/board" render={()=> <Board 
							{...this.state}
							load={this.load}
							play={this.play}
							paused={this.paused}
							exitGame={this.exitGame}
							updateCountdown={this.updateCountdown}
							updateTimer={this.updateTimer}
							onClick={this.onClick}
							/>} />
					</Router>
				</div>
			</div>
		)
	}
}

export default App;